<script>
  import { createEventDispatcher } from 'svelte'

  export let touchWrapper = false
  export let selected = false
  export let title
  export let leadingIcon = ''
  export let trailingIcon = ''
  export let color = ''
  export let className = ''

  let element
  const dispatch = createEventDispatcher()

  $: if (element && color) element.style.setProperty('--chip-color', color)

  function onTrailingIconClick() {
    dispatch('trailingIconClick')
  }
</script>

<div bind:this={element} class="mdc-chip {className}" class:mdc-chip--touch={touchWrapper}
  class:mdc-chip--selected={selected} class:colored={color} {title} on:click>
  <div class="mdc-chip__ripple"></div>
  {#if leadingIcon}
    <i class="material-icons-outlined mdc-chip__icon mdc-chip__icon--leading">
      {leadingIcon}
    </i>
  {/if}
  <span class="chip-label" role="gridcell">
    <span tabindex="0" class="mdc-chip__primary-action">
      <span class="mdc-chip__text">
        <slot></slot>
      </span>
    </span>
  </span>
  {#if trailingIcon}
    <span role="gridcell">
      <i class="material-icons-outlined mdc-chip__icon mdc-chip__icon--trailing" tabindex="-1"
        role="button" on:click={onTrailingIconClick}>
        {trailingIcon}
      </i>
    </span>
  {/if}
</div>

<style src="Chip.scss">.mdc-chip {
  max-width: 100%;
}
.mdc-chip .chip-label {
  max-width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
}

.colored {
  color: var(--chip-color);
  border: 1px solid var(--chip-color);
  background-color: initial;
}
.colored.mdc-chip.mdc-chip--selected:hover {
  color: var(--chip-color);
}
.colored .mdc-chip__ripple::before, .colored .mdc-chip__ripple::after {
  background-color: var(--chip-color);
}
.colored:hover .mdc-chip__ripple::before {
  opacity: 0.1;
}
.colored .mdc-chip__icon {
  color: var(--chip-color);
}

/*# sourceMappingURL=Chip.svelte.css.map */</style>
